<template>
    <div>
        <h1>Login</h1>

        <b>Test users:</b> (admin / password)

        <hr/>

        <form v-on:submit.prevent="login()">
            <table><tr>
                <td>Username:</td>
                <td><input v-model="data.body.username" /></td>
            </tr><tr>
                <td>Password:</td>
                <td><input v-model="data.body.password" type="password" /></td>
            </tr><tr>
                <td></td>
                <td><label><input v-model="data.rememberMe" type="checkbox" /> Remember Me</label></td>
            </tr><tr>
                <td></td>
                <td><label><input v-model="data.fetchUser" type="checkbox" /> Fetch User (test)</label></td>
            </tr><tr>
                <td></td>
                <td><button type="submit">Login</button>
                <router-link :to="{name: 'Register'}">register</router-link></td>
            </tr></table>

            <hr/>

            <div v-show="error" style="color:red; word-wrap:break-word;">{{ error }}</div>
        </form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                context: 'login context',
                data: {
                    body: {
                        username: 'admin',
                        password: 'password'
                    },
                    rememberMe: false,
                    fetchUser: false
                },
                error: null
            };
        },
        mounted() {
            console.log(this.$auth.redirect());
            // Can set query parameter here for auth redirect or just do it silently in login redirect.
        },
        methods: {
            login() {
                var redirect = this.$auth.redirect();
                this.$auth.login({
                    data: this.data.body, // Axios
                    rememberMe: this.data.rememberMe,
                    redirect: {name: redirect ? redirect.from.name : 'Skill'},
                    fetchUser: this.data.fetchUser
                })
                .then(() => {
                    console.log('success ' + this.context);
                    console.log(this.$auth);
                    this.$auth.redirect();
                }, (res) => {
                    console.log('error ' + this.context);
                    this.error = res.data;
                });
            }
        }
    }
</script>


